import makeStyles from "@mui/styles/makeStyles";
import Container from "@mui/material/Container";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import Paper from "@mui/material/Paper";
import React from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Banner } from "../../components";
import BottomNav from "../../components/BottomNav";
import TopNav from "../../components/TopNav";
import Check from "@mui/icons-material/Check";
import { connect } from "react-redux";
import { formatNumber } from "../../helpers/common/utility";
import labPng from "../../assets/laboratorium.png";
import moment from "moment";
import { setAppointment } from "../../redux/actions";
const useStyles = makeStyles(() => ({
  textRoll: {
    position: "relative",
    paddingBottom: "128px",
    paddingTop: "16px",
  },
}));
const PaketDetailPage = (props) => {
  const navigate = useNavigate();
  const { statePaket, actSetAppointment, stateUser, stateBahasa } = props;
  const classes = useStyles();
  const { id } = useParams();
  const [produk, setProduk] = React.useState({
    package_name: null,
    total: 0,
    update_at: "",
    list: [],
  });
  React.useEffect(() => {
    if (id && statePaket) {
      let found = statePaket.find((el) => el.ref_pkt_mcu_id === Number(id));
      if (found) {
        setProduk({
          package_name: found.produk.ref_prod_nama,
          tgl_update: moment(found.produk.tgl_update).format(
            "YYYY-MM-DD hh:mm"
          ),
          total: found.produk_paket.reduce(
            (a, b) => a + b.produk.ref_prod_hargajual,
            0
          ),
          list: found.produk_paket,
        });
      }
    }
  }, [id, statePaket]);
  const handlePilih = () => {
    actSetAppointment({
      layanan: {
        id_poli: 22,
        ref_layanan_nama: "MCU",
        disabled: true,
      },
      dokter: {
        peg_id: "100080",
        nama_dokter: "DOKTER LAB",
        disabled: true,
      },
      total: produk.total,
    });
    navigate("/make-appointment");
  };
  return (
    <Container className={classes.textRoll}>
      <Stack spacing={2}>
        <TopNav type="back" title={"Berita"} click={() => navigate(-1)} />
        <Banner variant="news" src={labPng} />
        <Container>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Stack spacing={0}>
                <Typography variant="subtitle2">
                  {produk.package_name}
                </Typography>
                <Typography variant="h5">
                  {`${formatNumber(produk.total)}*`}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {stateBahasa === "indonesia"
                    ? "*Harga dapat berubah sewaktu-waktu"
                    : "*The price could changes every time"}
                </Typography>
                <Typography variant="body1">
                  {`updated : ${produk.tgl_update}`}
                </Typography>
              </Stack>
            </Grid>
            <Grid item xs={12}>
              <Button
                variant="contained"
                size="small"
                onClick={handlePilih}
                disabled={!stateUser}
              >
                {stateBahasa === "indonesia" ? "Pilih" : "*Choose"}
              </Button>
            </Grid>
          </Grid>
        </Container>
        <Paper
          sx={{
            padding: "16px",
          }}
        >
          <Grid container spacing={1}>
            {produk.list.map((item, index) => {
              return (
                <Grid
                  item
                  xs={12}
                  container
                  spacing={1}
                  key={index + "_list"}
                  alignItems="center"
                >
                  <Grid item xs={2}>
                    <Check />
                  </Grid>
                  <Grid item xs>
                    <Typography variant="body1">
                      {item.produk.ref_prod_nama}
                    </Typography>
                  </Grid>
                </Grid>
              );
            })}
          </Grid>
        </Paper>
      </Stack>
      <BottomNav />
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    statePaket: state.master.paketMCU,
    stateBahasa: state.app.bahasa,
    stateUser: state.auth.token,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actSetAppointment: (body) => dispatch(setAppointment(body)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PaketDetailPage);
